<template >
    <div class="todo_form">
        <div class="form_name">Новая задача</div>
        <form @submit.prevent="">
            <div class="form_block">
                <label>Заголовок:</label>
                <input v-model="titleInput" type="text">
                <label>Задача:</label>
                <input v-model="bodyInput" type="text">
            </div>
            <div class="form_controller">
                <button @click="addTodo">ADD</button>
            </div>
        </form>
    </div>
</template>
<script>
export default {
    name: 'todo-form',
    props: ['user'],
    data() {
        return {
            titleInput: '',
            bodyInput: ''
        }
    },
    methods: {
        addTodo: function() {
            if(this.titleInput || this.bodyInput !== ''){
                this.user.todos = [...this.user.todos, {
                    id: this.user.todos.length,
                    text: this.bodyInput,
                    title: this.titleInput,
                    complete: false
                }]
                this.bodyInput = ''
                this.titleInput = ''
            }
        },
    }
}
</script>
<style>
    .todo_form {
        border-bottom: 1px solid rgb(133, 132, 132);
        padding: 10px;
    }
    .todo_form form {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
    .form_block {
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        gap: 5px;
    }
    .form_controller {
        display: flex;
        justify-content: space-between;
    }
    .container {
        display: flex;
        align-items: center;
        gap: 10px;
    }
    .form_name {
        font-size: 30px;
        text-align: center;
        color: rgb(133, 132, 132);
    }
    input {
        font-size: 15px;
        padding: 7px;
        border: 1px solid rgb(133, 132, 132);
        border-radius: 7px;
        outline: none;
    }
    input:focus {
        border-color: rgb(72, 156, 171);
    }
    .form_controller button {
        cursor: pointer;
        border: none;
        border-radius: 10px;
        padding: 10px 10%;
        background-color: rgb(72, 156, 171);
        color: white;
    }
</style>