<template>
    <header class="header">
        <div class="logo">TodoApp</div>
        <div class="container">
            <div v-show="user.isAuth">complete: {{completeTodos.length}}/{{user.todos.length}}</div>
            <div :class="{status_active: user.isAuth}" class="status">{{statusMode}}</div>
            <button @click="logIn">{{statusModeButton}}</button>
        </div>
    </header>
</template>
<script>
export default {
    name: 'app-header',
    props: [
        'user',
        'logIn'
    ],
    computed: {
        // Статус пользователя, онлайн/оффлайн
        statusMode: function() { return this.user.isAuth ? 'Вы в сети' : 'Не в сети' },
        // Статус сети на кнопке авторизации
        statusModeButton: function() { return this.user.isAuth ? 'Выйти' : 'Войти' },
        // Количество выполненых тасков у юзера, ввыодимых в поле 'complete'
        completeTodos: function() {
            return this.user.todos.filter((todo) => todo.complete !== false)
        }
    }
}
</script>
<style>
    .header {
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        padding: 10px;
        border-bottom: 1px solid rgb(133, 132, 132);
        background-color: white;
    }
    .header .logo {
        font-size: 25px;
    }
    .container {
        display: flex;
        align-items: center;
        gap: 20px;
    }
    .container button {
        cursor: pointer;
        padding: 7px 30px;
        background-color: rgb(72, 156, 171);
        color: whitesmoke;
        border: none;
        border-radius: 10px;
    }
    .status {
        width: 90px;
        color: rgb(194, 50, 84);
    }
    .status_active {
        color: green;
    }
</style>