<template>
    <div class="todo_item">
        <div class="todo_container">
            <input class="todo_checkbox" v-model="todo.complete" type="checkbox">
            <div class="todo_info">
                <div :class="{complete: todo.complete}">{{todo.title}}</div>
                <div :class="{complete: todo.complete}" class="text">{{todo.text}}</div>
            </div>
        </div>
        <button class="todo_btn" @click="removeTodo(todo.id)">Удалить</button>
    </div>
</template>
<script>
export default {
    props: ['todo', 'removeTodo'],
    name: 'todo-item'
}
</script>
<style>
    .todo_item {
        padding: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid rgb(133, 132, 132);
    }
    .todo_btn {
        cursor: pointer;
        border-radius: 10px;
        border: none;
        padding: 6px 4%;
        background-color: rgb(194, 50, 84);
        color: white;
    }
    .todo_container {
        display: flex;
        align-items: center;
        gap: 10px;
    }
    .todo_container input {
        cursor: pointer;
    }
    .todo_info .text {
        color: rgb(133, 132, 132);
        font-size: 14px;
    }
    .complete {
        text-decoration:line-through;
        color: rgb(162, 162, 162);
    }
</style>