<template>
    <div>
        <app-header :user="user" :logIn="logIn"/>
        <div v-if="user.isAuth" class="app">
            <todo-form :user="user"/>
            <todo-list :todos="user.todos" :removeTodo="removeTodo"/>
        </div>
        <div v-else>
            <error-page/>
        </div>
    </div>
</template>
<script>
import AppHeader from "./App-header.vue"
import TodoForm from "./TodoForm.vue"
import TodoList from "./TodoList.vue"
import ErrorPage from "./Error-page.vue"

export default {
    data() {
        return {
            user: {
                todos: [
                    {id: 1, text: 'Do something', title: 'Do', complete: false},
                    {id: 2, text: 'Walk to store', title: 'Walk', complete: false},
                    {id: 3, text: 'Get drunk with friends today', title: 'Drunk', complete: false},
                    {id: 4, text: 'Run in the morning', title: 'Run', complete: true}
                ],
                isAuth: true
            }
        }
    },
    methods: {
        logIn: function() {
            this.user.isAuth = !this.user.isAuth
        },
        removeTodo: function(id) {
            this.user.todos = this.user.todos.filter((todo) => todo.id !== id )
        }
    },
    components: {
        AppHeader, TodoForm, TodoList, ErrorPage
    }
}
</script>
<style>
    *{
        margin: 0;
        padding: 0;
        font-family: Arial, Helvetica, sans-serif;
        color: rgb(79, 82, 82);
    }
    .app {
        margin: 0 auto;
        margin-top: 60px;
        width: 50%;
    }
</style>